<template>
  <div class="basic">
    <el-table :data="reslist" border style="width: 100%">
      <el-table-column prop="username" label="用户名"> </el-table-column>
      <el-table-column label="头像" width="180">
        <template slot-scope="scope">
          <div>
            <img
              :src="scope.row.avatar"
              style="width:80px;border-radius: 50%;"
              alt=""
            />
          </div>
        </template>
      </el-table-column>
      <el-table-column label="评分">
        <template slot-scope="scope">
          <div>
            <i
              class="el-icon-star-on"
              v-for="(item, index) in scope.row.score"
              :key="index"
              style="color:red;"
            ></i>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="rateType" label="评价类型">
        <template slot-scope="scope">
          <div>
            {{ scope.row.rateType ? "不满意" : "满意" }}
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="deliveryTime" label="送达时间"> </el-table-column>
      <el-table-column prop="text" label="内容"> </el-table-column>
      <el-table-column label="推荐">
        <template slot-scope="scope">
          <div slot="recommend" class="name-wrapper">
            <el-tag
              style="margin-right:5px;"
              size="medium"
              v-for="(item, index) in scope.row.recommend"
              :key="index"
              >{{ item }}</el-tag
            >
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="rateTime" label="评价时间">
        <template slot-scope="scope">
          <div>
            {{ scope.row.rateTime | filTime }}
          </div>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="24"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //当前页数
      pageNum: 1,
      //每页条数
      pageSize: 5,
      list: [],
    };
  },
  mounted() {
    this.$axios.get("/tab.json").then((res) => {
      this.list = res.data;
      console.log(this.list);
    });
  },
  computed: {
    reslist() {
      return this.list.slice(
        this.pageSize * (this.pageNum - 1),
        this.pageSize * this.pageNum
      );
    },
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.pageNum = val;
    },
  },
  filters: {
    filTime(val) {
      var time = new Date(val);
      var year = time.getFullYear();
      var month = time.getMonth() + 1;
      var day = time.getDate();
      var hour = time.getHours();
      var minutes = time.getMinutes();
      var second = time.getSeconds();
      return `${year}-${month
        .toString()
        .padStart(2, 0)}-${day
        .toString()
        .padStart(2, 0)} ${hour
        .toString()
        .padStart(2, 0)}:${minutes
        .toString()
        .padStart(2, 0)}:${second.toString().padStart(2, 0)}`;
    },
  },
};
</script>

<style lang="scss" scoped></style>
